.contact {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(413px, 1fr));
  justify-items: end;

  &__info {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: (repeat(2, minmax(152px, 1fr)));
  }

  &__title {
    font-size: 14px;
    line-height: 27px;
    color: $text-color-light;
  }

  &__link {
    color: $text-color;
    cursor: pointer;

    &:active {
      color: $text-color-light;
    }
  }

  &__form {
    width: 413px;
  }
}

@media screen and (max-width: 480px) {
  .contact {
    &__form {
      width: 320px;
    }
  }
}
